import React from 'react';

import './Content.css';

export class Behandling extends React.Component{

  render(){
    var content = <div>
      <h2>Behandling</h2>
      <h3>Sjukgymnastik - Första Besöket</h3>
      <p>
        Vid det första besöket görs en grundlig undersökning och kartläggning av din nuvarande status. Utifrån det ställs en funktionsdiagnos. Du får med dig ett individuellt anpassat rehabiliteringsprogram som följs upp kontinuerligt. Rehabiliteringen kan bestå av träning, manuella tekniker, akupuktur eller tejpning.
      </p>

      <h3>Rörelseanalys</h3>
      <p>
        Genom olika tester går vi igenom ditt rörelsemönster med syftet att upptäcka och medvetandegöra ogynsamma rörelser. Utifrån det bygger vi upp ett träningsprogram som hjälper dig att plocka fram potentialen i ditt rörelsemönster!
Rörelseanalys är ett bra redskap för dig som vill förbättra din teknik, röra dig lättare och arbeta skadeförebyggande.
      </p>

      <h3>Idrottsmassage</h3>
      <p>
        Djupgående massage med syfte att öka blodcirkulation och påskynda återhämtning.
      </p>

      <p>
        Kliniken ligger i Sportgym Åres lokaler och här har vi tillgång till fullt utrustat gym.
      </p>
    </div>

    return(
      <div className="container">
        <div className="cont-dev">

          <img src={require('./Behandling/beh-bild1.JPG')} alt={'Behandling-bild'} width="100%" />
            {content}
        </div>
        <div className="cont-web">
              {content}
        </div>
      </div>
      )
  }
}



