import React from 'react';

import './Content.css';

export class Bokning extends React.Component{

  render(){
    var content = <div>
      <h2>Bokning</h2>
      <h3>Bokamera</h3>
      <a className="button"
        href="https://www.facebook.com/cecilia.kalla" target="_blank" rel="noopener noreferrer">Boka</a>
      <h3>Kontakt</h3>
      <ul className="cont">
        <li><a className="mail" href="mailto:Cecilia.kalla@gmail.com?Subject=Bokning%20C.K%20FYSIO" target="_top">Cecilia.kalla@gmail.com</a></li>
        <li>073-8447359</li>
      </ul>
    </div>

    return(
      <div className="container">
        <div className="cont-dev">

          <img src={require('./Bokning/bok-bild1.jpg')} alt={'Bokning bild1'} width="100%" />
            {content}
        </div>
        <div className="cont-web">
            <div className="left">
            <img src={require('./Bokning/bok-bild1.jpg')} alt={'Bokning bild1'} max-width="40%" />
              </div>
              {content}
        </div>
      </div>
      )
  }
}



