import React from 'react';

import './Content.css';

export class Priser extends React.Component{

  render(){
    var content = <div>
      <h2>Priser</h2>
      <ul className="cont">
        <li>Sjukgymnastik/Fysioterapi 50 min Första besöket 500 kr</li>
        <li>Sjukgymnastik/ Fysioterapi 25 min återbesök 350 kr</li>
        <li>Massage 55 min 600 kr</li>
        <li>Massage 25 min 350 kr</li>
        <li>Rörelseanalys 750 kr</li>
      </ul>
    </div>

    return(
      <div className="container">
        <div className="cont-dev">

        <img src={require('./Home/hem-bild1.png')} alt={'hej'} width="100%" />
            {content}
        </div>
        <div className="cont-web">
              {content}
        </div>
      </div>
      )
  }
}



