import React from 'react';

import './Content.css';

export class Presentation extends React.Component{

  render(){
    var content = <div>
      <h2>Presentation</h2>
      <p>C.K Fysio är en sjukgymnastklinik som finns i Åre.  Vår spetskompetens är idrottsskador, men du kan även vända dig till oss med andra smärtor från rörelseapparaten. Vi jobbar rehabiliterande och skadeförebyggande för elitaktiva, motionärer och barn/ungdom. Vi använder oss av behandlingsmetoder som kommer från den senaste forskningen och målet är att få dig tillbaka till önskad aktivitetsnivå.
      </p>

      <p>
        Till oss kan du komma privat eller via din försäkring då vi har avtal med ett flertal försäkringsbolag .
      </p>

      <p>På kliniken jobbar Cecilia Kalla. Hon är Leg.sjukgymnast och har drivit kliniken sedan 2016. Hon har jobbat som sjukgymnast för elitidrottare, idrottsgymnasium samt motionärer sedan 2011. Cecilia har vidareutbildning inom idrottsmedicin, rörelseanalyser och träning.
      </p>

      <ul className="cont">
        <li>Leg. Sjukgymnast: Examen från Umeå Universitet 2011</li>
        <li>Idrottsmedicin: Behandling av idrottsrelaterade skador.</li>
        <li>OMT steg 1 (ortopedisk manuell terapi): Manipulering- och mobiliseringstekniker för att behandla led, muskel eller nerv. </li>
        <li>Akupunktur</li>
        <li>Lic. Peronlig tränare</li>
        <li>Cert. FMS </li>
        <li>Aspera Movment systems</li>
        <li>Kinesiologitejpning och idrottstejpning</li>
        <li>Löpcoach via SISU</li>
        <li>Diverse utbildningar inom styrketräning samt gruppträning. </li>
      </ul>
    </div>

    return(
      <div className="container">
        <div className="cont-dev">
          <img src={require('./Home/hem-bild1.png')} alt={'hej'} width="100%" />
            {content}
        </div>
        <div className="cont-web">
          <div className="left">
            <img src={require('./Home/hem-bild1.png')} alt={'hej'} width="100%" />
          </div>
            {content}
        </div>
      </div>
      )
  }
}



